import { Icon as Iconify } from "@iconify/react";
import {
    Box,
    Container,
    InputAdornment,
    ListItem,
    ListItemButton,
    ListItemIcon,
    ListItemText,
    SvgIcon,
    TextField
} from "@mui/material";
import { useOutletContext } from "react-router";

import { useComponent } from "../../../../Application";
import FormSection from "../../../../components/FormSection";
import useAfterFirstRender from "../../../../utils/useAfterFirstRender";
import useDialog from "../../../../utils/useDialog";
import { useState } from "react";
import SingleFieldDialog from "../../../../components/SingleFieldDialog";
import LocationPicker from "../../../../components/LocationPicker";


const InvoicesSettings = () => {
    const { form } = useOutletContext();

    const { handleSubmit, getValue, setValue } = form;

    const [fieldDialogProps, setFieldDialogProps] = useState({ open: false });
    const openField = (renderContent, options = {}) => {
        return () => {
            setFieldDialogProps({
                renderContent,
                ...options,
                open: true,
            });
        };
    };

    useAfterFirstRender(() => { handleSubmit() }, [
        getValue("online-booking:pricing:default"),
        getValue("online-booking:name"),
        getValue("online-booking:headline"),
        getValue("online-booking:about"),
        getValue("online-booking:tel"),
        getValue("online-booking:website"),
        getValue("online-booking:instagram"),
        getValue("online-booking:address"),
        getValue("online-booking:latlng")
    ]);

    const PricingSelectDialog = useComponent("Pricing.PricingsDialog");
    const {
        isOpen: isPricingsDialogOpen,
        close: closePricingsDialog,
        open: openPricingsDialog
    } = useDialog(false);

    return (
        <>
            <Container maxWidth="lg">
                <FormSection
                    title="تعرفه"
                    description="مشخصات مرتبط با تعرفه و قیمت گذاری های فاکتور"
                >
                    <ListItem disableGutters disablePadding>
                        <ListItemButton
                            selected
                            onClick={openPricingsDialog}
                        >
                            <ListItemIcon>
                                <SvgIcon
                                    component={Iconify}
                                    icon="solar:tag-price-linear"
                                />
                            </ListItemIcon>
                            <ListItemText
                                primary={getValue("online-booking:pricing:default")?.name || "-"}
                                secondary={
                                    <>
                                        <span>تعرفه پیشفرض</span>
                                        {" - "}
                                        <small>
                                            تعرفه‌ای که بصورت پیشفرض در فاکتورها انتخاب می‌گردد.
                                        </small>
                                    </>
                                }
                            />
                        </ListItemButton>
                    </ListItem>
                </FormSection>

                <FormSection
                    title="هویت کسب‌وکار"
                    description="مشخصات مرتبط با هویت کسب و کار که در منو دیجیتال به کاربران نمایش داده خواهد شد."
                >
                    <ListItem disableGutters disablePadding>
                        <ListItemButton
                            selected
                            onClick={openField(
                                ({ value, setValue }) => (
                                    <TextField
                                        InputLabelProps={{ shrink: true }}
                                        fullWidth={true}
                                        label="نام کسب‌وکار"
                                        // variant="filled"
                                        value={value}
                                        onChange={(e) =>
                                            setValue(e.target.value)
                                        }
                                        autoFocus
                                    />
                                ),
                                {
                                    title: "نام کسب‌و‌کار",
                                    defaultValue: getValue("online-booking:name"),
                                    onSubmit: (value) => {
                                        setValue("online-booking:name", value);
                                        setFieldDialogProps({
                                            open: false,
                                        });
                                    },
                                }
                            )}
                        >
                            <ListItemIcon>
                                <SvgIcon
                                    component={Iconify}
                                    icon="solar:user-id-linear"
                                />
                            </ListItemIcon>
                            <ListItemText
                                primary={getValue("online-booking:name") || "-"}
                                secondary={
                                    <>
                                        <span>نام کسب‌وکار</span>
                                        {" - "}
                                        <small>
                                            نام کسب‌وکار که در منو دیجیتال نمایش
                                            داده می‌شود.
                                        </small>
                                    </>
                                }
                            />
                        </ListItemButton>
                    </ListItem>

                    <Box sx={{ my: 1 }} />

                    <ListItem disableGutters disablePadding>
                        <ListItemButton
                            selected
                            onClick={openField(
                                ({ value, setValue }) => (
                                    <TextField
                                        InputLabelProps={{ shrink: true }}
                                        fullWidth={true}
                                        label="شعار"
                                        // variant="filled"
                                        value={value}
                                        onChange={(e) =>
                                            setValue(e.target.value)
                                        }
                                        autoFocus
                                    />
                                ),
                                {
                                    title: "شعار",
                                    defaultValue: getValue("online-booking:headline"),
                                    onSubmit: (value) => {
                                        setValue("online-booking:headline", value);
                                        setFieldDialogProps({
                                            open: false,
                                        });
                                    },
                                }
                            )}
                        >
                            <ListItemIcon>
                                <SvgIcon
                                    component={Iconify}
                                    icon="solar:flag-linear"
                                />
                            </ListItemIcon>
                            <ListItemText
                                primary={getValue("online-booking:headline") || "-"}
                                secondary={
                                    <>
                                        <span>شعار</span>
                                        {" - "}
                                        <small>
                                            متن کوتاهی که در منو زیر عنوان
                                            مجموعه نمایش داده می‌شود.
                                        </small>
                                    </>
                                }
                            />
                        </ListItemButton>
                    </ListItem>

                    <Box sx={{ my: 1 }} />

                    <ListItem disableGutters disablePadding>
                        <ListItemButton
                            selected
                            onClick={openField(
                                ({ value, setValue }) => (
                                    <TextField
                                        InputLabelProps={{ shrink: true }}
                                        fullWidth={true}
                                        label="درباره ما"
                                        // variant="filled"
                                        multiline
                                        rows={3}
                                        value={value}
                                        onChange={(e) =>
                                            setValue(e.target.value)
                                        }
                                        autoFocus
                                    />
                                ),
                                {
                                    title: "درباره ما",
                                    defaultValue: getValue("online-booking:about"),
                                    onSubmit: (value) => {
                                        setValue("online-booking:about", value);
                                        setFieldDialogProps({
                                            open: false,
                                        });
                                    },
                                }
                            )}
                        >
                            <ListItemIcon>
                                <SvgIcon
                                    component={Iconify}
                                    icon="solar:question-circle-linear"
                                />
                            </ListItemIcon>
                            <ListItemText
                                primary={getValue("online-booking:about") || "-"}
                                secondary={
                                    <>
                                        <span>درباره ما</span>
                                        {" - "}
                                        <small>شرح مختصری که در صفحه رزرو به کاربران نمایش داده می‌شود.
                                        </small>
                                    </>
                                }
                            />
                        </ListItemButton>
                    </ListItem>
                </FormSection>

                <FormSection
                    title="اطلاعات تماس"
                    description="اطلاعات تماس کسب‌وکار که در صفحه معرفی کسب‌وکار به کاربران نمایش داده می‌شود."
                >
                    <ListItem disableGutters disablePadding>
                        <ListItemButton
                            selected
                            onClick={openField(
                                ({ value, setValue }) => (
                                    <TextField
                                        InputLabelProps={{ shrink: true }}
                                        fullWidth={true}
                                        label="شماره تماس"
                                        // variant="filled"
                                        value={value}
                                        onChange={(e) =>
                                            setValue(e.target.value)
                                        }
                                        autoFocus
                                    />
                                ),
                                {
                                    title: "شماره تماس",
                                    defaultValue: getValue("online-booking:tel"),
                                    onSubmit: (value) => {
                                        setValue("online-booking:tel", value);
                                        setFieldDialogProps({
                                            open: false,
                                        });
                                    },
                                }
                            )}
                        >
                            <ListItemIcon>
                                <SvgIcon
                                    component={Iconify}
                                    icon="solar:phone-calling-rounded-linear"
                                />
                            </ListItemIcon>
                            <ListItemText
                                primary={getValue("online-booking:tel") || "-"}
                                secondary={<span>شماره تماس</span>}
                            />
                        </ListItemButton>
                    </ListItem>

                    <Box sx={{ my: 1 }} />

                    <ListItem disableGutters disablePadding>
                        <ListItemButton
                            selected
                            onClick={openField(
                                ({ value, setValue }) => (
                                    <TextField
                                        InputLabelProps={{ shrink: true }}
                                        fullWidth={true}
                                        label="وب‌سایت"
                                        // variant="filled"
                                        value={value}
                                        onChange={(e) =>
                                            setValue(e.target.value)
                                        }
                                        InputProps={{
                                            startAdornment: <InputAdornment position="start">https://</InputAdornment>
                                        }}
                                        dir="ltr"
                                        autoFocus
                                    />
                                ),
                                {
                                    title: "وب‌سایت",
                                    defaultValue: getValue("online-booking:website"),
                                    onSubmit: (value) => {
                                        setValue("online-booking:website", value);
                                        setFieldDialogProps({
                                            open: false,
                                        });
                                    },
                                }
                            )}
                        >
                            <ListItemIcon>
                                <SvgIcon
                                    component={Iconify}
                                    icon="solar:global-linear"
                                />
                            </ListItemIcon>
                            <ListItemText
                                primary={getValue("online-booking:website") || "-"}
                                secondary={<span>وب‌سایت</span>}
                            />
                        </ListItemButton>
                    </ListItem>

                    <Box sx={{ my: 1 }} />

                    <ListItem disableGutters disablePadding>
                        <ListItemButton
                            selected
                            onClick={openField(
                                ({ value, setValue }) => (
                                    <TextField
                                        InputLabelProps={{ shrink: true }}
                                        fullWidth={true}
                                        label="اینستاگرام"
                                        // variant="filled"
                                        value={value}
                                        onChange={(e) =>
                                            setValue(e.target.value)
                                        }
                                        InputProps={{
                                            startAdornment: <InputAdornment position="start">https://instagram.com/</InputAdornment>
                                        }}
                                        dir="ltr"
                                        autoFocus
                                    />
                                ),
                                {
                                    title: "اینستاگرام",
                                    defaultValue: getValue("online-booking:instagram"),
                                    onSubmit: (value) => {
                                        setValue("online-booking:instagram", value);
                                        setFieldDialogProps({
                                            open: false,
                                        });
                                    },
                                }
                            )}
                        >
                            <ListItemIcon>
                                <SvgIcon
                                    component={Iconify}
                                    icon="ph:instagram-logo"
                                />
                            </ListItemIcon>
                            <ListItemText
                                primary={getValue("online-booking:instagram") || "-"}
                                secondary={<span>اینستاگرام</span>}
                            />
                        </ListItemButton>
                    </ListItem>

                    <Box sx={{ my: 1 }} />

                    <ListItem disableGutters disablePadding>
                        <ListItemButton
                            selected
                            onClick={openField(
                                ({ value, setValue }) => (
                                    <TextField
                                        InputLabelProps={{ shrink: true }}
                                        fullWidth={true}
                                        label="آدرس"
                                        multiline
                                        rows={3}
                                        // variant="filled"
                                        value={value}
                                        onChange={(e) =>
                                            setValue(e.target.value)
                                        }
                                        autoFocus
                                    />
                                ),
                                {
                                    title: "آدرس",
                                    defaultValue: getValue("online-booking:address"),
                                    onSubmit: (value) => {
                                        setValue("online-booking:address", value);
                                        setFieldDialogProps({
                                            open: false,
                                        });
                                    },
                                }
                            )}
                        >
                            <ListItemIcon>
                                <SvgIcon
                                    component={Iconify}
                                    icon="solar:map-point-linear"
                                />
                            </ListItemIcon>
                            <ListItemText
                                primary={getValue("online-booking:address") || "-"}
                                secondary={<span>آدرس</span>}
                            />
                        </ListItemButton>
                    </ListItem>

                    <Box sx={{ my: 1 }} />

                    <ListItem disableGutters disablePadding>
                        <ListItemButton
                            selected
                            onClick={openField(
                                ({ value, setValue }) => (
                                    <LocationPicker
                                        defaultValue={getValue("online-booking:latlng")}
                                        value={value}
                                        onChange={newValue => setValue(newValue)}
                                        sx={{ height: '200px', maxWidth: '100%' }}
                                    />
                                ),
                                {
                                    title: "موقعیت روی نقشه",
                                    defaultValue: getValue("online-booking:latlng"),
                                    onSubmit: (value) => {
                                        setValue("online-booking:latlng", value);
                                        setFieldDialogProps({
                                            open: false,
                                        });
                                    },
                                }
                            )}
                        >
                            <ListItemIcon>
                                <SvgIcon
                                    component={Iconify}
                                    icon="solar:map-point-wave-linear"
                                />
                            </ListItemIcon>
                            <ListItemText
                                primary={getValue("online-booking:latlng") || "-"}
                                secondary={<span>موقعیت روی نقشه</span>}
                            />
                        </ListItemButton>
                    </ListItem>
                </FormSection>
            </Container>

            <SingleFieldDialog
                {...fieldDialogProps}
                onClose={() => setFieldDialogProps({ open: false })}
            />

            <PricingSelectDialog
                selectable={true}
                open={isPricingsDialogOpen}
                onClose={closePricingsDialog}
                selected={getValue("online-booking:pricing:default")}
                onChange={(pricing) => {
                    setValue('online-booking:pricing:default', pricing);
                    closePricingsDialog();
                }}
                onClear={() => {
                    setValue('online-booking:pricing:default', '');
                    closePricingsDialog();
                }}
            />
        </>
    );
};

export default InvoicesSettings;
