import 'leaflet/dist/leaflet.css';
import Box from '@mui/material/Box';
import L from 'leaflet';
import { useState } from 'react';
import { MapContainer, Marker, TileLayer, useMapEvents } from 'react-leaflet';


delete L.Icon.Default.prototype._getIconUrl;

L.Icon.Default.mergeOptions({
    iconRetinaUrl: require('leaflet/dist/images/marker-icon-2x.png'),
    iconUrl: require('leaflet/dist/images/marker-icon.png'),
    shadowUrl: require('leaflet/dist/images/marker-shadow.png')
});

const MapEvents = ({ onDrag, onDragEnd }) => {
    useMapEvents({
        drag: (e) => {
            const { lat, lng } = e.target.getCenter();
            onDrag([lat, lng]);
        },
        dragend: (e) => {
            const { lat, lng } = e.target.getCenter();
            onDragEnd([lat, lng]);
        }
    });
    return null;
}

const LocationPicker = ({ defaultValue, value, onChange, ...props }) => {
    const [position, setPosition] = useState(!value || value.length < 1 ? defaultValue : value)


    return (
        <Box
            component={MapContainer}
            center={position}
            zoom={17}
            scrollWheelZoom={false}
            {...props}
        >
            <MapEvents onDrag={setPosition} onDragEnd={onChange} />
            <TileLayer
                attribution='&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors'
                url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
            />
            <Marker position={position} />
        </Box>
    )
}

export default LocationPicker;